import React from "react";
import img1 from "../image/product/1.png";
import img2 from "../image/product/2.png";
import img3 from "../image/product/3.png";
import img4 from "../image/product/4.png";
import img5 from "../image/product/5.png";
import img6 from "../image/product/6.png";
import Product from "./product";
const Section8 = () => {
  return (
    <section id="section8">
      <div className="col-12 text-white d-flex flex-column justify-content-center align-items-center heading">
        <span class="text-success font-weight-bold animation-element fade-in-3s">
          Delicious dishes
        </span>
        <h1 className="display-3 animation-element slide-from-bottom-3s">
          Chief special
        </h1>
        <p className="text-center text-secondary2 animation-element slide-from-bottom-3s">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, eiusmod
          tempor incididunt ut labore et dolore.
        </p>
      </div>
      <div className="col-10 mx-auto text-white my-5 pb-4 div-bottom">
        <div className="row mx-0 justify-content-center">
          <Product
            name="Chicken Nuggets"
            rate="4.2"
            img={img1}
            img_animation="animation-element animation-2s roll-from-left"
            animation="animation-element animation-3s scaleY-from-top"
          />
          <Product
            name="Noodles Salad"
            rate="4.1"
            img={img2}
            img_animation="animation-element animation-3s roll-from-left"
            animation="animation-element animation-4s scaleY-from-top"
          />
          <Product
            name="Bacon and Saucc"
            rate="4.4"
            img={img3}
            img_animation="animation-element animation-4s roll-from-left"
            animation="animation-element animation-5s scaleY-from-top"
          />
          <Product
            name="Brown Noodles"
            rate="4.4"
            img={img4}
            img_animation="animation-element animation-5s roll-from-left"
            animation="animation-element animation-6s scaleY-from-top"
          />
          <Product
            name="Chinese Roll"
            rate="4.2"
            img={img5}
            img_animation="animation-element animation-6s roll-from-left"
            animation="animation-element animation-7s scaleY-from-top"
          />
          <Product
            name="Chicken Soup"
            rate="4.5"
            img={img6}
            img_animation="animation-element animation-7s roll-from-left"
            animation="animation-element animation-8s scaleY-from-top"
          />
        </div>
        <div className="animation-element fade-in-3s text-center mt-5">
          <a
            class="btn btn-outline-secondary font-weight-bold ml-2 px-4 py-2 mt-4 "
            href="#"
          >
            SEE OUR MENU
          </a>
        </div>
      </div>
    </section>
  );
};

export default Section8;
