import React from "react";

const Product = ({ img, name, rate, animation, img_animation }) => {
  return (
    <div class="card product" style={{ width: "8rem" }}>
      <img
        src={img}
        alt="product"
        height="100px"
        width="100px"
        className={"mb-n5 mx-auto " + img_animation}
      />
      <div className={animation}>
        <div class="card-body">
          <h6 className="pt-5  text-center">{name}</h6>
        </div>
        <div class="card-footer mt-n4">
          <span className="d-flex justify-content-center align-items-center pb-4 pt-4 mt-1">
            {rate} <i class="pl-2 bx bxs-star small text-warning"></i>
          </span>
        </div>
      </div>
    </div>
  );
};

export default Product;
