import React from "react";

const Section7 = () => {
  return (
    <section id="section7">
      <div className="col-12 div-top d-flex flex-column justify-content-center">
        <div className="pl-5 ml-5 text-white">
          <span className="text-success font-weight-bold mr-5 pr-5 pt-2 pb-3">
            Become a chief
          </span>
          <h1 class="display-4 font-weight-bold animation-element slide-from-bottom-3s mb-n2 mt-4">
            Love to cook?
          </h1>
          <h1 class="display-4 font-weight-bold animation-element slide-from-bottom-3s">
            Join us
          </h1>
          <div className="animation-element fade-in-3s">
            <a
              class="btn btn-outline-secondary font-weight-bold ml-2 px-4 py-2 mt-4 "
              href="#"
            >
              SEE OUR MENU
            </a>
          </div>
        </div>
      </div>
      <div className="col-12 div-bottom text-white d-flex flex-column justify-content-center align-items-center mt-4">
        <span class="text-success font-weight-bold  mt-5 animation-element fade-in-3s">
          Make a reservation
        </span>
        <h1 className="display-3 animation-element slide-from-bottom-3s">
          Got special plans?
        </h1>
        <p className="text-center text-secondary2 animation-element slide-from-bottom-3s">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, eiusmod
          tempor incididunt ut labore et dolore.
        </p>
        <form className="w-100 my-3">
          <div class="form-row col-10 mx-auto">
            <div class="form-group col-4 animation-element slide-from-bottom-5s">
              <input
                type="text"
                class="form-control py-4 "
                id="inputDate"
                placeholder="Date"
              />
            </div>
            <div class="form-group col-4 animation-element slide-from-bottom-6s">
              <input
                type="text"
                class="form-control py-4"
                id="inputTime"
                placeholder="Time"
              />
            </div>
            <div class="form-group col-4 animation-element slide-from-bottom-7s">
              <input
                type="text"
                class="form-control py-4"
                id="inputGuests"
                placeholder="Guests"
              />
            </div>
          </div>
          <div className="mt-3 animation-element fade-in-3s">
            <button
              type="submit"
              class="btn btn-success d-flex mx-auto px-4 py-2 font-weight-bold"
            >
              RESERVE
            </button>
          </div>
        </form>
      </div>
    </section>
  );
};

export default Section7;
